} from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      datalist: [],
      htmlTitle: "导出pdf",
      isNewProject:false,
      companyId:'',
      cName:'',
      page: 1,
    }
  },
  mounted() {

  },
  created() {
    if(this.$route.query.companyId!=undefined){
      this.isNewProject = true;
      this.companyId=this.$route.query.companyId;
      this.cName=this.$route.query.cName;
    }
    if(this.$route.query.page){
      this.page=this.$route.query.page;
    }
    creditInfo({
      idStr: this.$route.query.id
    }).then(res => {
      if (res.data.msgNo == 1) {
        this.datalist = res.data.payload;
      }
    })
  },
  computed: {},
  methods: {}
}

</script>
